.footer__copyright {
  font-family: 'Inter', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: rgba(84, 84, 84, 1);
  margin: 0;
}

@media screen and (max-width: 598px) {
  .footer__copyright {
    font-size: 14px;
    line-height: 17px;
    margin: 0 0 0 19px;
  }
}
